import React, {Component} from 'react';
import classes from './Name.css';
import { Link } from 'react-router-dom';

class Name extends Component{
    render () {
        return (
            <div className = {classes.Name}>
                <Link to="/">
                    <p>Kal Exam Hai!</p>
                </Link>
            </div>
        )
    }
}

export default Name;


