import React from 'react';
import classes from './Logo.css';
import logo from '../../../assets/images/logo.png'
import { Link } from 'react-router-dom';

const Logo = (props) => (
    <div className={classes.Logo}>
        <Link to="/">
            <img src= {logo} alt="Logo"/>
        </Link>
    </div>
);

export default Logo;


